$font-mercury: 'Mercury Display A', 'Mercury Display B', serif;

$font-avenirnext: 'AvenirNextLTPro', sans-serif;

$global-width: 72rem;

$rr-max-width: $global-width;

$rr-grid-divisions: 2;

$rr-blend-mode: multiply;

$rr-grid-settings: ( 
    s: ( 
        mq: max-width, 
        size: 35rem, 
        columns: 4, 
        gutter: 1rem / $rr-grid-divisions, 
        offset: 1rem, 
        vspace: 2,
        column_color: rgba(200, 200, 255, .2),
        baseline_color: rgba(128, 128, 200, .2)
    ), 
    m: ( 
        mq: min-width, 
        size: 35rem, 
        gutter: 1rem,
        columns: 8, 
        offset: 2rem,
        vspace: 3, //  21px / 42px
        column_color: rgba(255, 200, 200, .2),
        baseline_color: rgba(128, 128, 200, .2)        
    ), 
    l: ( 
        size: 48rem, 
        columns: 12,
        column_color: rgba(128, 255, 200, .2),
        baseline_color: rgba(128, 128, 200, .2)        
    ), 
    xl: ( 
        size: $global-width + 4rem, 
        offset: 0,
        gutter: 2rem, //36px 
        vspace: 5, // (36px / 72px)
        column_color: rgba(200, 128, 255, .2),
        baseline_color: rgba(128, 128, 200, .2)        
    )
);

$rr-grids: (
    2-up: (
        m : (
            columns: 2,
        )
    ),
    3-up: (
        m : (
            columns: 3,
        )
    ),
    4-up: (
        m : (
            columns: 2,
        ),
        l : (
            columns: 4,
        )
    ),
    5-up: (
        m : (
            columns: 3,
        ),
        l : (
            columns: 5,
        )        
    ),
    6-up: (
        m : (
            columns: 3,
        ),
        l : (
            columns: 4,
        ),
        xl : (
            columns: 6
        )
    ),
    sidebar-left: (
        m: (
            columns: 3,
            areas : side main main
        ),
        l : (
            columns: 4,
            areas: side main main main
        )
    ),
    sidebar-right: (
        m: (
            columns: 3,
            areas: main main side,
            flex-columns: 2
        ),
        l : (
            columns: 4,
            areas: main main main side
        )
    ),
    major-left: (
        m: (
            columns: 2,
            areas: major minor,
            flex-columns: 2
        ),
        l : (
            columns: 3,
            areas: major major minor,
        )        
    ),
    major-right: (
        m: (
            columns: 2,
            areas: minor major,
            flex-columns: 2
        ),
        l : (
            columns: 3,
            areas: minor major major,
        )
    )
);

$lineheights: ( 
    tight: 0,
    medium: 1,
    wide: 2,
    huge: 3,
    redic: 4
);

$fonts: ( 
    s : (
        xs: .75rem,
        s: .875rem,
        m: 1rem,
        l: 1.125rem,
        xl: 1.25rem,
        2xl: 1.45rem,
        3xl: 1.67rem,
        4xl: 1.92rem,
        5xl: 2.24rem
    ),
    l : (
        2xl: 1.5rem,
        3xl: 2.25rem,
        4xl: 3rem,
        5xl: 4.5rem
    )
);

$font-sizes: rr-font-sizes();

$headings: (
    h1 : (
        size: 5xl,
        lineheight: tight,
        font: $font-mercury,
        weight: normal
    ),
    h2 : (
        size: 4xl,
        lineheight: tight,
        font: $font-mercury,        
        weight: normal,
    ),
    h3 : (
        size: 3xl,
        weight: normal,
    ),
    h4 : (
        size: 2xl,        
        weight: normal,
    ), 
    h5 : (
        size: xl,
        weight: normal,
        style: italic
    ),
    h6 : (
        size: l,
        weight: bold
    )
);

$colors: (
    wildcat-blue: #0033af,
    wildcat-white: #fff,
    wildcat-black: #2c2a29,
    light-blue: #1897d4,
    medium-blue: #143887,
    dark-blue: #202c5f,
    light-gray: #c8c8c7,
    medium-gray: #a5acaf,
    dark-gray: #63656a,
    success: lawngreen,
    warning: darkorange,
    alert: darkred,
    info: lightsteelblue,
);

$text-colors: (
    link: rebeccapurple,
    link-active: purple,
    text: color(wildcat-black),
    accent: color(alert),
);

$text-colors-dark: (
    link: lightskyblue,
    link-active: lightsteelblue,
    text: color(wildcat-white),
    accent: color(success)
);


// convenience aliases
$white: color(wildcat-white);
$black: color(wildcat-black);
$color-text: color(text);
$color-link: color(link);

// Set rr defaults

$rr-fonts: $fonts;
$rr-headings: $headings;
$rr-lineheights: $lineheights;



